// src/components/qa/PostQuestionDialog.jsx
import { useState } from "react";
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogHeader,
  DialogTitle,
  DialogFooter,
} from "@/components/ui/dialog";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Textarea } from "@/components/ui/textarea";
import { Label } from "@/components/ui/label";
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select";
import { Badge } from "@/components/ui/badge";
import { X, FileCode, Info } from "lucide-react";
import { Prism as SyntaxHighlighter } from "react-syntax-highlighter";
import { vscDarkPlus } from "react-syntax-highlighter/dist/esm/styles/prism";
import toast from "react-hot-toast";

const programmingLanguages = [
  "javascript",
  "python",
  "java",
  "c++",
  "ruby",
  "go",
  "rust",
  "typescript",
  "php",
  "swift",
];

const popularTags = [
  "react",
  "node.js",
  "express",
  "django",
  "spring",
  "vue.js",
  "angular",
  "docker",
  "kubernetes",
  "aws",
  "azure",
  "mongodb",
  "postgresql",
  "mysql",
  "redis",
];

export function PostQuestionDialog({ isOpen, onClose, onPost }) {
  const [formData, setFormData] = useState({
    title: "",
    content: "",
    code: "",
    language: "",
    tags: [],
  });

  const [selectedTags, setSelectedTags] = useState([]);
  const [previewMode, setPreviewMode] = useState(false);

  const handleSubmit = (e) => {
    e.preventDefault();

    if (!formData.title || !formData.content) {
      toast.error("Please fill in all required fields", {
        style: {
          background: "#363636",
          color: "#fff",
        },
      });
      return;
    }

    onPost({
      ...formData,
      tags: selectedTags,
    });

    // Reset form
    setFormData({
      title: "",
      content: "",
      code: "",
      language: "",
      tags: [],
    });
    setSelectedTags([]);
    onClose();
  };

  const handleTagClick = (tag) => {
    if (selectedTags.includes(tag)) {
      setSelectedTags(selectedTags.filter((t) => t !== tag));
    } else if (selectedTags.length < 5) {
      setSelectedTags([...selectedTags, tag]);
    } else {
      toast.error("Maximum 5 tags allowed", {
        style: {
          background: "#363636",
          color: "#fff",
        },
      });
    }
  };

  return (
    <Dialog open={isOpen} onOpenChange={onClose}>
      <DialogContent className="sm:max-w-[800px]">
        <DialogHeader>
          <DialogTitle>Ask a Question</DialogTitle>
          <DialogDescription>
            Be specific and provide enough details to help others understand
            your question.
          </DialogDescription>
        </DialogHeader>

        <form onSubmit={handleSubmit} className="space-y-6">
          <div className="space-y-2">
            <Label htmlFor="title">Title</Label>
            <Input
              id="title"
              placeholder="e.g., How to implement JWT authentication in Next.js?"
              value={formData.title}
              onChange={(e) =>
                setFormData({ ...formData, title: e.target.value })
              }
              className="w-full"
            />
          </div>

          <div className="space-y-2">
            <div className="flex items-center justify-between">
              <Label>Description</Label>
              <Button
                type="button"
                variant="ghost"
                size="sm"
                onClick={() => setPreviewMode(!previewMode)}
                className="text-sm"
              >
                {previewMode ? "Edit" : "Preview"}
              </Button>
            </div>
            {previewMode ? (
              <div className="min-h-[200px] p-4 rounded-md border bg-slate-50 dark:bg-slate-900 prose prose-sm dark:prose-invert max-w-none">
                {formData.content}
              </div>
            ) : (
              <Textarea
                placeholder="Describe your problem in detail..."
                value={formData.content}
                onChange={(e) =>
                  setFormData({ ...formData, content: e.target.value })
                }
                className="min-h-[200px]"
              />
            )}
          </div>

          <div className="space-y-2">
            <Label>Code (optional)</Label>
            <div className="flex gap-4">
              <div className="flex-1">
                <Select
                  value={formData.language}
                  onValueChange={(value) =>
                    setFormData({ ...formData, language: value })
                  }
                >
                  <SelectTrigger>
                    <SelectValue placeholder="Select language" />
                  </SelectTrigger>
                  <SelectContent>
                    {programmingLanguages.map((lang) => (
                      <SelectItem key={lang} value={lang}>
                        {lang}
                      </SelectItem>
                    ))}
                  </SelectContent>
                </Select>
              </div>
            </div>
            {previewMode && formData.code ? (
              <SyntaxHighlighter
                language={formData.language}
                style={vscDarkPlus}
                className="!bg-slate-900 rounded-lg"
                showLineNumbers
              >
                {formData.code.trim()}
              </SyntaxHighlighter>
            ) : (
              <Textarea
                placeholder="Paste your code here..."
                value={formData.code}
                onChange={(e) =>
                  setFormData({ ...formData, code: e.target.value })
                }
                className="font-mono"
              />
            )}
          </div>

          <div className="space-y-2">
            <Label>Tags</Label>
            <div className="flex flex-wrap gap-2 mb-2">
              {selectedTags.map((tag) => (
                <Badge
                  key={tag}
                  variant="secondary"
                  className="bg-blue-100 dark:bg-blue-900 text-blue-700 dark:text-blue-100 cursor-pointer group"
                  onClick={() => handleTagClick(tag)}
                >
                  {tag}
                  <X className="w-3 h-3 ml-1 group-hover:text-blue-900 dark:group-hover:text-blue-50" />
                </Badge>
              ))}
            </div>
            <div className="flex flex-wrap gap-2">
              {popularTags
                .filter((tag) => !selectedTags.includes(tag))
                .map((tag) => (
                  <Badge
                    key={tag}
                    variant="outline"
                    className="cursor-pointer hover:bg-blue-50 dark:hover:bg-blue-900/50"
                    onClick={() => handleTagClick(tag)}
                  >
                    {tag}
                  </Badge>
                ))}
            </div>
          </div>

          <DialogFooter>
            <Button
              type="button"
              variant="outline"
              onClick={onClose}
              className="hover:bg-red-50 dark:hover:bg-red-900/50"
            >
              Cancel
            </Button>
            <Button
              type="submit"
              className="bg-gradient-to-r from-blue-500 to-purple-500 text-white"
            >
              Post Question
            </Button>
          </DialogFooter>
        </form>
      </DialogContent>
    </Dialog>
  );
}