// src/components/qa/QuestionFilter.jsx
import { Input } from "@/components/ui/input";
import { Button } from "@/components/ui/button";
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select";
import { Badge } from "@/components/ui/badge";
import { Search, X } from "lucide-react";

const popularTags = [
  "react",
  "javascript",
  "python",
  "node.js",
  "machine-learning",
  "aws",
  "database",
  "api",
];

export function QuestionFilter({
  selectedTags,
  onTagsChange,
  sortBy,
  onSortChange,
  filterSolved,
  onFilterSolvedChange,
}) {
  const handleTagClick = (tag) => {
    if (selectedTags.includes(tag)) {
      onTagsChange(selectedTags.filter((t) => t !== tag));
    } else {
      onTagsChange([...selectedTags, tag]);
    }
  };

  return (
    <div className="space-y-4">
      <div className="flex flex-col md:flex-row gap-4">
        <div className="relative flex-1">
          <Search className="absolute left-3 top-2.5 h-4 w-4 text-muted-foreground" />
          <Input placeholder="Search questions..." className="pl-10" />
        </div>

        <Select value={sortBy} onValueChange={onSortChange}>
          <SelectTrigger className="w-[160px]">
            <SelectValue placeholder="Sort by" />
          </SelectTrigger>
          <SelectContent>
            <SelectItem value="newest">Newest</SelectItem>
            <SelectItem value="votes">Most Votes</SelectItem>
            <SelectItem value="views">Most Views</SelectItem>
          </SelectContent>
        </Select>

        <Select value={filterSolved} onValueChange={onFilterSolvedChange}>
          <SelectTrigger className="w-[160px]">
            <SelectValue placeholder="Filter by status" />
          </SelectTrigger>
          <SelectContent>
            <SelectItem value="all">All Questions</SelectItem>
            <SelectItem value="solved">Solved Only</SelectItem>
            <SelectItem value="unsolved">Unsolved Only</SelectItem>
          </SelectContent>
        </Select>
      </div>

      <div>
        <div className="flex flex-wrap gap-2 mb-2">
          {selectedTags.map((tag) => (
            <Badge
              key={tag}
              variant="secondary"
              className="bg-blue-100 dark:bg-blue-900 text-blue-700 dark:text-blue-100 cursor-pointer group hover:bg-blue-200 dark:hover:bg-blue-800"
              onClick={() => handleTagClick(tag)}
            >
              {tag}
              <X className="w-3 h-3 ml-1 group-hover:text-blue-900 dark:group-hover:text-blue-50" />
            </Badge>
          ))}
        </div>
        <div className="flex flex-wrap gap-2">
          {popularTags
            .filter((tag) => !selectedTags.includes(tag))
            .map((tag) => (
              <Badge
                key={tag}
                variant="outline"
                className="cursor-pointer hover:bg-blue-50 dark:hover:bg-blue-900/50"
                onClick={() => handleTagClick(tag)}
              >
                {tag}
              </Badge>
            ))}
        </div>
      </div>
    </div>
  );
}